<template>
  <div id="app">
    <router-view />
  </div>
</template>

<script>
import Cookies from "js-cookie";
export default {
  name: "App",
  created() {
    //处理登录逻辑
    let userInfo = JSON.parse(
      Cookies.get("userInfo") || sessionStorage.getItem("userInfo") || "{}"
    );
    if (userInfo.user_id && userInfo.password) {
      this.$post("user/login", userInfo)
        .then(res => {
          if (!res.user_id) {
            this.$notify({
              title: "提示",
              message: res,
              type: "warning"
            });
            Cookies.remove("userInfo");
            this.$router.replace("/");
            return;
          }
          sessionStorage.setItem("userInfo", JSON.stringify(userInfo));
          if (this.$route.path == "/")
            this.$router.replace("/page/user/userList");
        })
        .catch(res => {
          this.$router.replace("/");
          loading.close();
        });
    } else {
      this.$router.replace("/");
    }
  }
};
</script>

<style>
</style>



