import Vue from 'vue'
import Router from 'vue-router'
import pages from '@/pages/router'

Vue.use(Router)
export default new Router({
  mode: "history",
  base: "/cv/",
  routes: [
    {
      path: '*',
      component: () => import("@/pages/html/NotFound"),
    },
    {
      path: '/',
      name: 'login',
      component: () => import("@/pages/html/login"),
      meta: { title: "登录" }
    },
    {
      path: '/home',
      name: 'home',
      component: () => import("@/pages/html/home"),
      children: []
    },
    {
      path: '/page',
      name: 'page',
      component: () => import("@/pages/html/page"),
      children: [...pages]
    },
  ]
})


